import React from "react";
import "./Documentation.less";

export default function Documentation() {
    const docs = [
        "Приказ по основной деятельности № 0921/03 от 10.10.2022 «О внесении изменений в показатели Положения о стимулировании труда НПР УрФУ» (к приказу от 13.05.2022 № 467/03).pdf",
        "Приказ по основной деятельности № 0467/03 от 13.05.2022 «О введении Положения «О стимулировании труда научно-педагогических работников».pdf",
        "Приказ по основной деятельности № 259/03 от 25.03.2021 «О введении Положения «О стимулировании труда научно-педагогических работников».doc",
        "Приказ по основной деятельности № 562/03 от 03.07.2020 «О введении Положения «О стимулировании труда научно-педагогических работников».doc",
        "Приказ по основной деятельности № 707/03 от 21.08.2019 «О введении в действие версии 2 Положения «О требованиях к научным монографиям, подлежащим стимулированию».pdf"
    ];
    return (
        <div className="documentation bg-white">
            {docs.map((doc) => {
                const regex = /\.\w+$/g;
                const docName = doc.replaceAll(regex, '')
                const extension = doc.split('.').reverse()[0];
                return (
                    <div className="documentation__doc" key={doc}>
                        <i className={extension === "pdf"
                            ? "u-icon icon-pdf documentation-icon"
                            : extension === "doc"
                                ? "u-icon icon-doc documentation-icon" : ""}></i>
                        {/* <div className={extension === "pdf" ? "documentation__pdf-icon" : extension === "doc" ? "documentation__doc-icon" : ""}></div> */}
                        <h5>{docName}</h5>
                    </div>
                )
            })}
        </div>
    );
}